.projects-section {

    h2 {
        width: 50%;
        margin: auto;
        text-align: center;
    }

    .projects-list {
        padding-top: 20px;
        width: 100%;
        display:flex;
        align-items: center;
        justify-content: space-around;

        .project {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 10px;

            &-title {
                font-size: 30px;
                position: relative;

                &:after {
                    content: '';
                    height: 3px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 0;
                    background: $primaryColor;
                    transition: .3s ease;
                    
                }

                &:hover {
                    &:after {
                        width: 100%;
                        transition: .3s ease;
                    }
                }
            }

            .content-wrapper {
                height: 200px;
                width: 175px;
                position: relative;

                .project-image {
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                    z-index: 1;
                    animation: switchBack 1s ease;
                    cursor: pointer;

                    img {
                        height: 100%;
                        width: 100%;
                    }

                    &.animated {
                        animation: switch 1s ease;
                    }
                }

                .project-tech {
                    color: red;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                    background: rgba($bgColor, .3);
                    backdrop-filter: blur(4px);
                    animation: switchReverseBack 1s ease;
                    cursor: pointer;

                    &.animated {
                        animation: switchReverse 1s ease;
                    }
                }
            }
        }
    }
    
    .main-project {
        padding-top: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}

@keyframes switch {
    0% {
        top: 0;
    }

    50% {
        top: -50%;
    }

    100% {
        top: 0;
    }
}

@keyframes switchReverse {
    0% {
        top: 0;
    }

    50% {
        top: 50%;
    }

    100% {
        top: 0;
    }
}

@keyframes switchBack { 
    0% {
        top: 0;
    }

    50% {
        top: -50%;
    }

    100% {
        top: 0;
    }
}

@keyframes switchReverseBack {
    0% {
        top: 0;
    }

    50% {
        top: 50%;
    }

    100% {
        top: 0;
    }
}