* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: inherit;
}

::-webkit-scrollbar {
    display: none;
}

html {
    scroll-behavior: smooth;
}

body { 
    height: 100%;
    width: 100%;
    background-color: $bgColor;
    color: $primaryFont;
    font-family: 'Roboto Mono', monospace;
}

.text {
    line-height: 36px;
    position: relative;
    
    .cursor {
        font-size: 25px;
        margin-left: -5px;
    
        &.blink {
            animation: blink 1s infinite;
        }
    }
}

@keyframes blink {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.section {
    height: 100vh;

    @media screen and (min-width: 1024px) {
        width: calc(100% - 270px);
    }
}

